
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
body{
  color: #e65d6e;
}

#base {
  width: 100%;
  position: relative;
}
.side {
  width: 420px;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  position: fixed;
  top: 0;
  bottom: 0;
  z-index: 22;
  background: #42b983;
  left: 0px;
}
.content-outter {
  width: 100%;
  height: 100%;
  position: fixed;
}
.content {
  padding-left: 420px;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  width: 100%;
  min-height: 100vh;
  padding-bottom: 40px;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.lang-change {
  width: 100%;
  text-align: right;
  position: fixed;
  top: 40px;
  right: 32px;
}
.lang-change .flag {
  width: 20px;
  vertical-align: middle;
  margin-right: 14px;
}
.lang-change .lang-item {
  height: 34px;
  line-height: 34px;
  padding: 0 8px;
  cursor: pointer;
}
.lang-change .lang-item:hover {
  background: #EDF1F8;
  border-radius: 3px;
}
.copyright {
  position: fixed;
  left: 420px;
  bottom: 22px;
  color: #002059;
  opacity: 0.2;
  text-align: center;
  width: calc(100% - 420px);
}
@media screen and (max-width: 1000px) {
.side {
    display: none;
}
.content {
    padding-left: 0;
    width: 100%;
}
.copyright {
    left: 0;
    width: 100%;
}
}

#login {
  width: 300px;
}

#register {
  background-color: #B5DEF2;
  margin: 140px 0 140px auto;
}
#register .loginBox {
  background-color: #F0F4F6;
  border: 1px solid #BfD6E1;
  border-radius: 5px;
  color: #444;
  font: 14px 'Microsoft YaHei', '微软雅黑';
  margin: 0 auto;
  width: 388px;
}
#register .loginBox .loginBoxCenter {
  border-bottom: 1px solid #DDE0E8;
  padding: 24px;
}
#register .loginBox .loginBoxCenter p {
  margin-bottom: 10px;
  text-align: left;
}
#register .loginBox .loginBoxCenter .loginInput {
  border: 1px solid #D2D9dC;
  border-radius: 2px;
  color: #444;
  font: 12px 'Microsoft YaHei', '微软雅黑';
  padding: 8px 14px;
  margin-bottom: 8px;
  width: 310px;
}
#register .loginBox .loginBoxCenter .loginInput :focus {
  border: 1px solid #B7D4EA;
  -webkit-box-shadow: 0 0 8px #B7D4EA;
          box-shadow: 0 0 8px #B7D4EA;
}
#register .loginBox .loginBoxButtons {
  border-top: 0px solid #FFF;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  line-height: 28px;
  overflow: hidden;
  padding: 20px 24px;
  vertical-align: center;
  filter: alpha(opacity=80);
  -moz-opacity: 0.5;
  opacity: 0.5;
}
#register .loginBox .loginBoxButtons .loginBtn {
  background-image: -moz-linear-gradient(to bottom, blue, #85CFEE);
  border: 1px solid #98CCE7;
  border-radius: 20px;
  -webkit-box-shadow: inset rgba(255, 255, 255, 0.6) 0 1px 1px, rgba(0, 0, 0, 0.1) 0 1px 1px;
          box-shadow: inset rgba(255, 255, 255, 0.6) 0 1px 1px, rgba(0, 0, 0, 0.1) 0 1px 1px;
  color: #444;
  cursor: pointer;
  font: bold 13px Arial;
  padding: 10px 50px;
}
